<template>
  <div class="back">
    <app-bar
      back-icon
      title="发布历史"
      hide-user-menu
      hideMessage
      hideDemand
    ></app-bar>
    <v-content style="min-height: calc(100vh - 64px)" class="mt-8 mb-8">
      <v-container>
        <v-card class="mx-auto" min-width="800">
          <div class="ml-5 mr-5">
            <el-table style="width: 100%" stripe>
              <el-table-column
                label="项目名称"
                prop="entryname"
              ></el-table-column>
              <el-table-column label="创建者" prop="contacts"></el-table-column>
              <el-table-column label="创建时间" prop="time"></el-table-column>
              <el-table-column label="状态" prop="status"></el-table-column>
              <el-table-column>
                <template slot="header">
                  <el-input
                    v-model="search"
                    size="mini"
                    placeholder="输入关键字搜索"
                  />
                </template>
                <template slot-scope="scope">
                  <el-button
                    size="mini"
                    @click="handleEdit(scope.$index, scope.row)"
                    >编辑</el-button
                  >
                  <el-button
                    size="mini"
                    type="danger"
                    @click="handleDelete(scope.$index, scope.row)"
                    >删除</el-button
                  >
                </template>
              </el-table-column>
            </el-table>
          </div>
        </v-card>
      </v-container>
    </v-content>
    <app-footer></app-footer>
  </div>
</template>

<script>
import AppBar from '@/components/App/AppBar'
import AppFooter from '@/components/App/AppFooter'

export default {
  name: 'PersonalCenter',
  components: {
    AppBar,
    AppFooter
  },
  data() {
    return {
      tableData: [],
      search: ''
    }
  },
  mounted() {
    this.getList()
  },
  methods: {
    handleEdit() {},
    handleDelete() {},
    async getList() {
      let res = await this.$http.get('/api/school/readMydraft?page=1')
      this.tableData = res.data.data.Sci.list
    }
  }
}
</script>

<style scoped>
.back {
  background-color: #f2f2f2;
}
</style>
